import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    task: {},
    completedTasks: [
        { id: 1, title: " ", description: "  1", statusCompleted: true },
        { id: 2, title: " ", description: "  2", statusCompleted: true },
        { id: 3, title: " ", description: "  3", statusCompleted: true },
        { id: 4, title: " ", description: "  4", statusCompleted: true },
        { id: 5, title: "' ", description: "  5", statusCompleted: true },
    ],
    notCompletedTasks: [
        { id: 6, title: " ", description: "  6", statusCompleted: false },
        { id: 7, title: " ", description: "  7", statusCompleted: false },
        { id: 8, title: " ", description: "  8", statusCompleted: false },
        { id: 9, title: " ", description: "  9", statusCompleted: false },
        { id: 10, title: " ", description: "  10", statusCompleted: false },
    ],
};

export const appSlice = createSlice({
    name: "tasks",
    initialState,
    reducers: {
        openTask: (state, action) => {
            if (action.payload.id === 0) {
                state.task = {};
            } else {
                const hasObjectWithId = state.completedTasks.some((obj) => obj.id === action.payload.id);
                if (hasObjectWithId) {
                    const foundObject = state.completedTasks.find((obj) => obj.id === action.payload.id); ///        
                    state.task = foundObject;
                } else {
                    const foundObject = state.notCompletedTasks.find((obj) => obj.id === action.payload.id); ///        
                    state.task = foundObject;
                }
            }
        },
        addTask: (state, action) => {
            const newId = state.completedTasks.length + state.notCompletedTasks.length + 1;
            const updateTask = { ...action.payload, id: newId };
            if (action.payload.statusCompleted === true) {
                state.completedTasks.unshift(updateTask);
            } else {
                state.notCompletedTasks.unshift(updateTask);
            }
        },
        editTask: (state, action) => {
            const hasObjectWithId = state.completedTasks.some((obj) => obj.id === action.payload.id);
            if (hasObjectWithId) {
                const newList = state.completedTasks.map((obj) => {
                    //   
                    if (obj.id === action.payload.id) {
                        return { ...action.payload };
                    }
                    return obj;
                });
                const foundObject = newList.find((obj) => obj.statusCompleted === false); ///        
                if(foundObject!==undefined){state.notCompletedTasks.unshift(foundObject)}; //    
                const updateList = newList.filter((item) => item.statusCompleted === true); //       
                state.completedTasks = updateList;
            } else {
                const newList = state.notCompletedTasks.map((obj) => {
                    //   
                    if (obj.id === action.payload.id) {
                        return { ...action.payload };
                    }
                    return obj;
                });
                const foundObject = newList.find((obj) => obj.statusCompleted === true); ///        
                if(foundObject!==undefined){state.completedTasks.unshift(foundObject)}; //    
                const updateList = newList.filter((item) => item.statusCompleted === false); //       
                state.notCompletedTasks = updateList;
            }
        },
        deleteTask: (state, action) => {
            const hasObjectWithId = state.completedTasks.some((obj) => obj.id === action.payload.id);
            if (hasObjectWithId) {
                const newList = state.completedTasks.filter((item) => item.id !== action.payload.id);
                state.completedTasks = newList;
            } else {
                const newList = state.notCompletedTasks.filter((item) => item.id !== action.payload.id);
                state.notCompletedTasks = newList;
            }
        },
    },
});

// Action creators are generated for each case reducer function
export const { openTask, addTask, editTask, deleteTask } = appSlice.actions;

export default appSlice.reducer;
